:root {
  --orange: hsl(37, 98%, 54%);
  --desaturated-blue: hsl(203, 28%, 79%);
  --dark-blue: hsl(205, 30%, 27%);
  --very-dark-blue: hsl(206, 45%, 15%);
  --grey: hsl(0, 0%, 95%);
  --light-blue: hsl(203, 22%, 55%);
  --blue: hsl(205, 37%, 55%);
  --white: hsl(0, 0%, 99%);
}

* {
  margin: 0;
  border: 0;
  box-sizing: border-box;
  font-family: "Atkinson Hyperlegible", sans-serif;
}

button {
  cursor: pointer;
}

button:hover {
  opacity: 0.7;
}

/* START SCREEN */

.start-screen {
  width: 100vw;
  height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;

  background-color: var(--very-dark-blue);
}

h1 {
  color: var(--white);
}

.create-board-card {
  width: 327px;
  height: 386px;
  padding: 20px;
  border-radius: 10px;

  display: flex;
  flex-direction: column;

  background-color: var(--white);
}

.create-board-card h2 {
  font-size: 15px;
  font-weight: bold;
  color: var(--blue);
}

.button {
  width: 50%;
  height: 40px;
  border-radius: 26px;

  font-size: 14px;
  font-weight: bold;

  background-color: var(--desaturated-blue);
  color: var(--white);
}

.button-active {
  width: 50%;
  height: 40px;
  border-radius: 26px;

  font-size: 14px;
  font-weight: bold;
  background-color: var(--dark-blue);
  color: var(--white);
}

.create-board-card button:hover {
  cursor: pointer;
}

.selection-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 5px;
  height: 50%;
}

.button-container {
  margin-top: 10px;

  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 5%;
}

#start-button {
  font-size: 18px;
  font-weight: bold;
  background-color: var(--orange);
  color: var(--white);
  border-radius: 26px;
  margin-top: 15px;

  height: 80px;
}

#start-button:hover {
  background-color: hsla(37, 100%, 65%, 1);
}

/* START SCREEN */

/* BOARD SCREEN */

.board-screen {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 30px;
  padding: 20px;
}

.board-screen-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.board-screen-header h2 {
  color: var(--very-dark-blue);
}

.board-screen-header button {
  padding: 8px 18px;
  border-radius: 23px;

  font-size: 16px;
  font-weight: 700;

  background-color: var(--orange);
  color: var(--white);
}

.board4 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  gap: 10px;
  justify-items: center;
  margin: 50px 0;
}

.board6 {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 7px;
  grid-template-rows: repeat(6, 1fr);
  justify-items: center;
  margin: 40px 0;
}

#show {
  background-color: var(--grey);
}

.board4 > button {
  border-radius: 50%;
  width: 65px;
  height: 65px;
  background-color: var(--very-dark-blue);
  color: var(--very-dark-blue);
  font-size: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.board6 > button {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  background-color: var(--very-dark-blue);
  color: var(--very-dark-blue);
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.board4 .board-buttons {
  background-color: var(--orange);
  color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
}

.board6 .board-buttons {
  background-color: var(--orange);
  color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
}

.board4 .board-buttons-match {
  background-color: var(--light-blue);
  color: var(--white);
}

.board6 .board-buttons-match {
  background-color: var(--light-blue);
  color: var(--white);
}

.solo-tracker {
  display: flex;
  justify-content: space-between;
}

.solo-cards {
  background-color: hsla(203, 25%, 90%, 1);
  width: 45%;
  height: 80px;
  border-radius: 10px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.solo-cards h3 {
  font-size: 15px;
  color: var(--blue);
}

.solo-cards p {
  font-size: 24px;
  color: var(--very-dark-blue);
  font-weight: bold;
}

.points-container {
  display: flex;
  justify-content: space-evenly;
}

.points-card {
  width: 23%;
  height: 70px;

  background-color: hsla(203, 25%, 90%, 1);
  border-radius: 5px;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
}

.points-current-card {
  width: 23%;
  height: 70px;
  border-radius: 5px;

  background-color: var(--orange);
  color: var(--white);

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
}

.points-card h3 {
  font-size: 15px;
  font-weight: 700;
  color: var(--blue);
}

.points-card p {
  font-size: 24px;
  font-weight: 700;
  color: var(--very-dark-blue);
}

.points-current-card h3 {
  font-size: 15px;
  font-weight: 700;
  color: var(--white);
}

.points-current-card p {
  font-size: 24px;
  font-weight: 700;
  color: var(--white);
}

.dots {
  display: flex;
  gap: 10px;
  position: absolute;
  top: -30%;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: var(--white);
  outline: 2px solid black;
}

.dot-active {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  outline: 2px solid black;
  background-color: var(--orange);
}

/* BOARD SCREEN */

/* PAUSE */

.pause {
  width: 100vw;
  height: 100vh;
  background-color: hsla(206, 28%, 10%, 0.6);

  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;

  display: flex;
  justify-content: center;
  align-items: center;
}

.pause-card {
  width: 90%;
  height: 40%;
  border-radius: 10px;
  padding: 10px;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;

  background-color: var(--white);
}

.pause-card-button {
  width: 90%;
  height: 25%;

  font-size: 20px;
  font-weight: bold;
  border-radius: 26px;

  background-color: hsl(203, 25%, 90%);
  color: var(--dark-blue);
}

.pause-restart {
  background-color: var(--orange);
  color: var(--white);
}

/* PAUSE */

/* WIN */

.win {
  width: 100vw;
  height: 100vh;
  background-color: hsla(206, 28%, 10%, 0.6);

  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;

  display: flex;
  justify-content: center;
  align-items: center;
}

.win-card {
  width: 90%;
  height: 80%;
  border-radius: 10px;
  padding: 10px;

  background-color: var(--white);

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.win-card > p {
  font-size: 14px;
  font-weight: bold;
  color: var(--light-blue);
}

.win-card-button {
  width: 90%;
  height: 15%;

  font-size: 20px;
  font-weight: bold;
  border-radius: 26px;

  background-color: hsl(203, 25%, 90%);
  color: var(--dark-blue);
}

.win-card-button-multi {
  width: 90%;
  height: 10%;

  font-size: 20px;
  font-weight: bold;
  border-radius: 26px;

  background-color: hsl(203, 25%, 90%);
  color: var(--dark-blue);
}

.win-card-button-1 {
  background-color: var(--orange);
  color: var(--white);
}

.win-card-button-1 {
  background-color: var(--orange);
  color: var(--white);
}

.win-subcard {
  width: 90%;
  height: 15%;
  padding: 20px;

  border-radius: 10px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  background-color: hsl(205, 25%, 90%);
}

.win-sub-card-multi {
  width: 90%;
  padding: 9px 20px;
  border-radius: 10px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  background-color: hsl(205, 25%, 90%);
}

.win-subcard h3 {
  font-size: 13px;
  font-weight: bold;

  color: var(--light-blue);
}

.win-subcard p {
  font-size: 20px;
  font-weight: bold;
}

.win-sub-card-multi h3 {
  font-size: 16px;
  font-weight: bold;

  color: var(--light-blue);
}

.win-sub-card-multi p {
  font-size: 20px;
  font-weight: bold;
}

/* WIN */

.off {
  display: none;
}

@media (min-width: 768px) {
  /* START SCREEN */

  h1 {
    font-size: 50px;
  }

  .create-board-card {
    width: 654px;
    height: 560px;
    padding: 40px;
  }

  .create-board-card h2 {
    font-size: 20px;
  }

  .button {
    height: 60px;
    font-size: 26px;
  }

  .button-active {
    height: 60px;
    font-size: 26px;
  }

  #start-button {
    height: 120px;
    font-size: 26px;
  }

  /* START SCREEN */

  /* BOARD */

  .board-screen {
    justify-content: space-evenly;
  }

  .board-screen-header {
    font-size: 40px;
  }

  .board-screen-header > button {
    font-size: 40px;
    width: 180px;
    height: 72px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .board4 {
    width: 532px;
    height: 532px;
    align-self: center;
  }

  .board6 {
    width: 572px;
    height: 572px;
    align-self: center;
  }

  .board4 > button {
    width: 82px;
    height: 82px;
    font-size: 56px;
  }

  .board6 > button {
    width: 62px;
    height: 62px;
    font-size: 44px;
  }

  .solo-tracker {
    justify-content: space-evenly;
  }

  .solo-cards {
    width: 255px;
    flex-direction: row;
    justify-content: space-between;
    padding: 0px 40px;
  }

  .solo-cards h3 {
    font-size: 24px;
  }

  .solo-cards p {
    font-size: 32px;
  }

  .points-card h3 {
    font-size: 24px;
  }

  .points-card p {
    font-size: 32px;
  }

  .points-card {
    height: 90px;
  }

  .points-current-card {
    height: 90px;
  }

  .points-current-card h3 {
    font-size: 24px;
  }

  .points-current-card p {
    font-size: 32px;
  }

  /* BOARD */

  /* PAUSE */

  .pause-card-button {
    font-size: 50px;
  }

  /* PAUSE */

  /* WIN */

  .win-card > p {
    font-size: 32px;
  }

  .win-card {
    width: 75%;
  }

  .win-card h2 {
    font-size: 50px;
  }

  .win-subcard {
    padding: 10px 40px;
  }

  .win-subcard h3 {
    font-size: 30px;
  }

  .win-subcard p {
    font-size: 38px;
  }

  .win-card-button {
    font-size: 40px;
  }

  .win-sub-card-multi {
    padding: 14px 40px;
  }

  .win-sub-card-multi h3 {
    font-size: 30px;
  }

  .win-sub-card-multi p {
    font-size: 38px;
  }

  .win-card-button-multi {
    font-size: 40px;
  }
  /* WIN */
}

@media (min-width: 1000px) {
  /* BOARD */

  .App {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .board-screen {
    width: 800px;
  }

  /* BOARD */

  .win-card {
    width: 45%;
  }

  .pause-card {
    width: 35%;
  }
}
